.step5__point {
    height:3rem;
    width:3rem;
    line-height:3rem;
    font-size:1.5rem;
    text-align:center;
    background:#fff;
    color:#000;
    transition: all 0.375s;
    position: absolute;
    margin-left:-1.5rem;
    margin-top:-1.5rem;
    cursor:default;
}

.step5__point--active {
    background:#ff0000;
    color:#fff;
    border-radius: 1.5rem;
}

@media screen and (min-width: 1920px) {
    .step5__point {
        height: 6rem;
        width: 6rem;
        line-height: 6rem;
        font-size: 3rem;
        margin-left:-3rem;
        margin-top:-3rem;
    }

    .step5__point--active {
        border-radius: 3rem;
    }
}



.step5__point--done {
    color:#ccc;
}

.step5__point--1 {
    top:33%;
    left:33%;
}

.step5__point--2 {
    top:33%;
    left:66%;
}

.step5__point--3 {
    top:66%;
    left:66%;
}

.step5__point--4 {
    top:66%;
    left:33%;
}

.step5__point--5 {
    top:50%;
    left:50%;
}