.mouse-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.mouse-container .box {
    display: flex;
    width:512px;
    max-width:45%;
    max-height:60vh;
    transition: all 0.375s;
}

.mouse-container .box:hover {
    cursor: pointer;
    background: rgb(0, 147, 233);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.mouse-container .box img {
    max-height:50vh;
}