.navigation {
    height:3rem;
    margin-bottom:0.75rem;
}

.navigation__contents {
    background:#fff;
    position:absolute;
    right:1.5rem;
    height:3rem;
    line-height:1.5rem;
    font-size:1.2rem;
    border-bottom-left-radius: 0.375rem;
    border-bottom-right-radius: 0.375rem;
}

.navigation__contents a {
    text-decoration: none;
    color:inherit;
    transition: all 0.375s;
}

.navigation__item:first-child a {
    border-bottom-left-radius: 0.375rem;
}

.navigation__item:last-child a {
    border-bottom-right-radius: 0.375rem;
}

.navigation__item kbd:first-child {
    margin-left:0;
}

.navigation__item kbd:last-child {
    margin-right:0;
}

.navigation__item a:hover, .navigation__button:hover {
    background-color: rgb(0, 147, 233);
    color:#fff;
    cursor: pointer;
}

.navigation__divider, .navigation__item {
    display: inline-block;
}

.navigation__divider {
    width: 2px;
    height:100%;
    background:#aaa;
    position:absolute;
}

.navigation__padding, .navigation__item a {
    padding:0.75rem;
}

.navigation__item kbd {
    margin-left:0.375rem;
    margin-right:0.375rem;
}

.navigation__item a {
    display: block;

}