.level-selector {
    min-height:100vh;
}

.level-selector__mobile {
    display: none;
}
@media (max-width: 1200px) or (max-height: 730px) {
    body {
        height:100vh;
        overflow:hidden;
    }

    .level-selector__mobile {
        display: block;
        position: fixed;
        height:100vh;
        overflow-y:scroll;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #fff;
        font-size:1rem;
        line-height:1.5rem;
        z-index:100;
    }

    .level-selector .level-selector__mobile h1 {
        color:black;
    }

    .level-selector__mobile p, .level-selector__mobile ul {
        padding-left:0.75rem;
        padding-right:0.75rem;
    }

    .level-selector__mobile ul {
        padding-left:1.5rem;
    }
}

.level-selector h1 {
    color:#fff;
    margin:0;
    text-align: center;
    padding-top:1.5rem;
    padding-bottom:3rem;
    line-height:3rem;
    font-size:3rem;
    text-transform: uppercase;
}

.level-selector__list {
    display: flex;
    flex-direction: row;
}

.level-selector__level {
    border-radius: 0.375rem;
    max-width: 256px;
    text-decoration: none;
    color: inherit;
    display: block;
    background:#fff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin-left:0.75rem;
    margin-right:0.75rem;
    margin-bottom:0.75rem;
    transition: all 0.375s;
}

.level-selector__level:hover, .level-selector__level--selected {
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    background: rgb(0, 147, 233);
    color:#fff;
}

.level-selector__level h2, .level-selector__level p {
    margin:0;
    padding: 0 0.75rem;
}

.level-selector__level p {
    padding-bottom:0.75rem;
}

.level-selector__levelimage svg {
    border-top-left-radius: 0.375rem;
    border-top-right-radius: 0.375rem;
}
