.language-selector__outer {
    display:flex;
    flex-direction: column;
    min-height: 100vh;
}

.language-selector {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width:100%;
}

.language-selector__help {
    text-align: center;
    display:none;
}

@media screen and (min-width: 720px){
    .language-selector__help {
        display: block;
    }
}

.language-selector__padding {
    padding-right:0.75rem;
}

.language-selector__contents {
    position:relative;
    width:1024px;
    max-width:100%;
    margin-left: auto;
    margin-right:auto;
    display: flex;
    flex-direction: row;
}

.language-selector__language {
    border-radius: 0.375rem;
    background:#fff;
    display: block;
    width:40%;
    margin-left: 5%;
    margin-right:5%;
    text-decoration: none;
    color:inherit;
    text-align: center;
    font-size:1.5rem;
    line-height:3rem;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    transition: all 0.375s;
}

.language-selector__language:hover, .language-selector__language--selected {
    background: rgb(0, 147, 233);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.language-selector__image {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin-bottom:1.5rem;
}

.language-selector__image svg {
    position:  absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    color: white;
    text-align: center;
}

.language-selector__image::before {
    content: "";
    display: block;
    padding-top: 100%;
}

.language-selector__languagename {
    text-transform: uppercase;
}